.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.sm{
  font-size: 18px;
  color: rgb(13, 82, 82);
}
.br{
  border: 1px solid black;
}
.df{
  display: flex;
}
.al{
  align-items: center;
}
.fdc{
  flex-direction: column;
}

.Home{
  display: flex;
}
.main{
  display: flex;
  flex: 5;
  border: 1px solid #61dafb;
}
.mr10{
  margin: 10px;
}
.pd10{
  padding: 10px;
}
.cp{
  cursor: pointer;
}
